@charset "UTF-8";
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: local("Lato"), url("/fonts/lato/Lato-Regular.woff2") format("woff2"), url("/fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: local("Oswald"), url("/fonts/oswald/oswaldregular.woff2") format("woff2"), url("/fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: local("Oswald"), url("/fonts/oswald/oswaldmedium.woff2") format("woff2"), url("/fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
* {
  box-sizing: border-box;
}

*::before {
  box-sizing: border-box;
}

*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  text-transform: uppercase;
  box-sizing: border-box;
  padding: 10px;
  width: 100%;
  color: #ffffff;
  background-color: #68b738;
  border: 0;
  text-align: center;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 0 20px;
  max-width: 100%;
}

@media (min-width: 768px) {
  .container {
    position: relative;
    margin: 0 auto;
    padding: 0 30px;
    max-width: 100%;
  }
}
@media (min-width: 1280px) {
  .container {
    position: relative;
    margin: 0 auto;
    padding: 0;
    width: 100%;
    max-width: 1220px;
  }
}
.page__body {
  position: relative;
  color: #000000;
  background-color: #ffffff;
  min-height: 100vh;
  min-width: 320px;
  margin: 0;
}

.page-header {
  position: relative;
}

.page-header__list {
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
  align-self: center;
  text-align: center;
}

.page-header__toggle {
  position: relative;
  display: block;
  justify-self: end;
  width: 26px;
  height: 24px;
  padding: 0;
  border: 0;
  cursor: pointer;
  background-color: transparent;
  z-index: 10;
}

.page-header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.page-header__wrapper--closed .navigation {
  display: none;
}
.page-header__wrapper--closed .page-header__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 26px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.page-header__wrapper--opened .navigation {
  display: block;
}
.page-header__wrapper--opened .page-header__toggle::before, .page-header__wrapper--opened .page-header__toggle::after {
  content: "";
  position: absolute;
  transform: translateY(50%);
  top: 50%;
  left: 0;
  width: 30px;
  height: 2px;
  background-color: #000000;
}
.page-header__wrapper--opened .page-header__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-header__wrapper--opened .page-header__toggle::after {
  transform: rotate(-45deg);
}

.page-header__logo {
  display: block;
  padding: 12px 0;
  z-index: 10;
}
.page-header__logo:hover {
  opacity: 0.8;
}
.page-header__logo:active {
  opacity: 0.6;
}
.page-header__logo--special {
  cursor: default;
}
.page-header__logo--special:active {
  opacity: inherit;
}
.page-header__logo--special:hover {
  opacity: inherit;
}

.page-header__wrapper--nojs .page-header__toggle {
  display: none;
}

@media (min-width: 768px) {
  .page-header__wrapper--closed .navigation {
    display: block;
  }

  .page-header {
    border-bottom: none;
  }

  .page-header__wrapper {
    padding: 12px 0 0;
  }

  .page-header__toggle {
    display: none;
  }
}
@media (min-width: 1280px) {
  .page-header__wrapper {
    padding: 42px 0 0;
  }

  .page-header__toggle {
    display: none;
  }

  .page-header--special::before {
    position: absolute;
    content: "";
    display: block;
    width: 50%;
    min-height: 694px;
    top: 0;
    right: 0;
    left: inherit;
    background-image: url("../../img/promo/promo-desktop-bg.jpg");
    background-color: #63a73a;
    background-position: 0%;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
.page-main {
  position: relative;
}

.page-footer {
  background-color: #f2f2f2;
}

.page-footer__inner {
  padding: 37px 0 0;
  display: grid;
}

.page-footer__logo-link {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 0;
  border-bottom: 2px solid #cdcdcd;
  padding: 0 0 18px;
  align-self: end;
  justify-self: center;
}
.page-footer__logo-link .page-footer:hover path {
  color: #666666;
}
.page-footer__logo-link .page-footer:active path {
  opacity: 0.3;
}

.page-footer__social-list {
  display: flex;
  flex-direction: row;
  gap: 28px;
  justify-content: center;
  align-items: center;
  list-style: none;
  padding: 19px 0;
  border-bottom: 2px solid #cdcdcd;
  margin: 0;
  font-size: 0;
}

.page-footer__social-icon {
  color: #666666;
}
.page-footer__social-icon:hover {
  color: #68b738;
}
.page-footer__social-icon:active {
  color: #68b738;
  opacity: 0.3;
}

.page-footer__logo-svg {
  color: #111111;
}
.page-footer__logo-svg:hover {
  color: #3e3e3e;
}
.page-footer__logo-svg:active {
  color: #6b6b6b;
}

.page-footer__copyright-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  justify-self: center;
  padding: 18px 0;
  transition: opasity 0.2s;
  width: 280px;
}
.page-footer__copyright-link:hover {
  color: #3e3e3e;
}
.page-footer__copyright-link:active {
  opacity: 0.3;
}
.page-footer__copyright-link:hover .page-footer__copyright-icon {
  color: #68b738;
}
.page-footer__copyright-link:active path {
  color: #68b738;
  opacity: 0.3;
}

.page-footer__copyright-icon {
  color: #666666;
}

.page-footer__copyright-text {
  margin: 0;
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #444444;
}

@media (min-width: 768px) {
  .page-footer__inner {
    padding: 36px 0 0;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
  }

  .page-footer__social-list {
    border: none;
    justify-self: center;
  }

  .page-footer__logo-link {
    border: none;
    text-align: left;
    align-self: center;
    padding: 0;
  }

  .page-footer__copyright-text {
    margin: 0;
    margin-left: 5px;
  }

  .page-footer__copyright-link {
    justify-self: end;
    width: 168px;
  }
}
@media (min-width: 1440px) {
  .page-footer__inner {
    padding: 50px 0 50px;
  }
}
