  margin: 0;
  padding: 0;
}

.catalog-select__item {
  display: block;
  position: relative;
  padding: 30px 20px 20px;
  background-color: #f2f2f2;
}
.catalog-select__item::before {
  content: "";
  top: 24px;
  left: 20px;
  position: absolute;
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #68b738;
}
.catalog-select__item::after {
  content: "";
  top: 20px;
  left: 27px;
  position: absolute;
  width: 36px;
  height: 50px;
  background-color: #000000;
  mask-image: url("../img/icons/stack.svg#catalog-select_slim");
  mask-size: 36px 50px;
  mask-repeat: no-repeat;
}
.catalog-select__item--fat::after {
  mask-image: url("../img/icons/stack.svg#catalog-select_fat");
  top: 37px;
  left: 12px;
  width: 67px;
  height: 28px;
  mask-size: 67px 28px;
}

.catalog-select__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 36.96px;
  color: #000000;
  text-transform: uppercase;
  text-align: left;
  margin: 0 0 30px;
  margin-left: 68px;
}

.catalog-select__subtitle {
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
  margin: 0 0 18px;
  padding: 0 0 22px;
  border-bottom: 2px solid #d9d9d9;
}

.catalog-select__link {
  position: relative;
  display: inline-block;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #000000;
  text-transform: uppercase;
  max-width: 150px;
}
.catalog-select__link:active {
  opacity: 0.3;
}
.catalog-select__link::after {
  content: "";
  position: absolute;
  top: 2px;
  width: 32px;
  height: 12px;
  margin-left: 17px;
  display: inline-block;
  background-image: url("../img/icons/stack.svg#catalog-select_arrow");
  background-repeat: no-repeat;
  background-position: -8px;
  transition: background-position 0.2s;
}
.catalog-select__link:hover::after {
  background-position: 0 center;
}

@media (min-width: 768px) {
  .catalog-select__list {
    gap: 30px;
  }

  .catalog-select__item {
    padding: 50px 53px 50px 53px;
    min-height: 372px;
  }
  .catalog-select__item::before {
    top: 90px;
    left: auto;
    right: 63px;
    position: absolute;
