.process__inner {
  padding: 19px 0 28px;
}

.process__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
  margin: 0 0 40px;
}

.process__list {
  list-style: none;
  padding-left: 80px;
  counter-reset: process-item 0;
}

.process__item {
  display: flex;
  position: relative;
  min-height: 60px;
  margin-bottom: 20px;
  align-items: center;
}
.process__item--leaf::after {
  position: absolute;
  content: "";
  right: calc(100% + 20px);
  top: 0;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-size: 30px 30px;
  background-image: url("../img/icons/stack.svg#process_leaf");
  background-position: center;
  background-repeat: no-repeat;
}
.process__item--powder::after {
  position: absolute;
  content: "";
  right: calc(100% + 20px);
  top: 0;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-size: 30px 30px;
  background-image: url("../img/icons/stack.svg#process_powder");
  background-position: center;
  background-repeat: no-repeat;
}
.process__item--eat::after {
  position: absolute;
  content: "";
  right: calc(100% + 20px);
  top: 0;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-size: 30px 32px;
  background-image: url("../img/icons/stack.svg#process_eat");
  background-position: center;
  background-repeat: no-repeat;
}
.process__item--timer::after {
  position: absolute;
  content: "";
  right: calc(100% + 20px);
  top: 0;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-size: 30px 32px;
  background-image: url("../img/icons/stack.svg#process_timer");
  background-position: center;
  background-repeat: no-repeat;
}

.process__description {
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
