.main-nav__toggle {
  top: 15px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 30px;
  height: 30px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  font-size: 0;
}
.main-nav__toggle:hover, .main-nav__toggle:active {
  background-color: transparent;
}

.main-nav--closed .site-list__item {
  display: none;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;
  width: 30px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 10px 0 0 #000000, 0 20px 0 0 #000000;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: fade(#000000, 30%);
  box-shadow: 0 6px 0 0 fade(#000000, 30%), 0 12px 0 0 fade(#000000, 30%);
}

.main-nav--opened .main-nav__toggle {
  top: 20px;
  right: 20px;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  transform: translateY(50%);
  left: 0;
  width: 30px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
  background-color: fade(#000000, 30%);
}

.main-nav--opened .main-nav__wrapper {
  position: absolute;
  top: 70px;
  right: 0;
  left: 0;
  z-index: 10;
  background-color: #ffffff;
}

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  align-self: center;
  text-align: center;
}

